.banner {
  width: 100vw !important;
  height: auto !important;
  color: #fff;
  position: relative;
  object-fit: contain;
}
@media only screen and (min-width: 1000px) {
  .banner {
    background-position-y: top !important;
  }
}

.banner-conteins {
  margin-left: 30px;
  padding-top: 140px;
  height: 190px;
}
.banner-title {
  font-size: 3rem;
  font-weight: 800;
  padding-bottom: 0.3rem;
}
.banner-description {
  width: 45rem;
  line-height: 1.3;
  padding-top: 1rem;
  font-size: 0.8rem;
  max-width: 360px;
  height: 80px;
}
.banner-fadebottom {
  height: 7.4rem;
  background-image: linear-gradient(
    180deg,
    transparent,
    rgba(37, 37, 37, 0.62),
    #111
  );
}
.banner-conteins button {
  text-align: center;
  outline: none;
  cursor: pointer;
  color: #fff;
  border: none;
  font-weight: 700;
  border-radius: 0.4vw 4vw;
  padding-left: 2rem;
  padding-right: 2rem;
  padding-top: 0.5rem;
  margin-right: 1rem;
  background-color: rgba(51, 51, 51, 0.5);
  padding-bottom: 0.5rem;
}
.banner-conteins button:hover {
  color: #000;
  background: #fff;
  transition: all 300ms ease;
}
